export default {
	data() {
		return {
			login: false,
			avatarUrl: "../../static/unlogin.png",
			userinfo: {}
		}
	},
	onLoad() {
		var _isLogin = getApp().globalData.isLogin;
		if(_isLogin){
			this.isLogin = true;
		}
	},
	onShow() {
		var _isLogin = getApp().globalData.isLogin;
		if(_isLogin){
			this.isLogin = true;
		}
	},
	methods: {
		goLogin() {
			if (!this.login) {
				uni.navigateTo({
					url: '../login/login',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	}
}
